body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  background: #1e2732;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.game-area-wrapper {
  width: 100%;
  height: 50vh;
  color: #009688;
  border-radius: 8px;
  position: relative;
  box-shadow: 0 0 10px #abbfc0 !important;
}

.game-area-wrapper .game-area-overlay {
  z-index: 50;
  width: 100%;
  height: 100%;
  background: #010a01;
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  box-shadow: 0 4px 30px rgba(0, 0, 0, .1);
}

.game-area-wrapper .game-area {
  background-image: url("game-area-background.53605dc7.png");
  width: 600px !important;
  height: 99% !important;
}

.game-area-wrapper h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 3rem;
}

.game-area-wrapper h1, .game-area-wrapper h3 {
  color: #fff;
  -webkit-animation: 1.5s infinite alternate flicker !important;
  animation: 1.5s infinite alternate flicker !important;
}

.game-area-wrapper .snake-body-part {
  width: 2.5% !important;
  height: 3% !important;
  border: 1px solid gray !important;
  border-radius: 30px !important;
}

.game-area-wrapper .food-wrapper {
  justify-content: center;
  align-items: center;
  padding: 2px;
  display: flex;
}

.game-area-wrapper .food-wrapper .food {
  width: 50% !important;
  height: 50% !important;
}

.game-area-wrapper .food-wrapper .food, .game-area-wrapper .food-wrapper {
  border: 1px solid red;
  border-radius: 10px;
  -webkit-animation: 1.5s infinite alternate pulsate;
  animation: 1.5s infinite alternate pulsate;
  box-shadow: 0 0 .3rem #fff, 0 0 .3rem #fff, 0 0 1rem red, 0 0 .8rem red, 0 0 1.8rem red, inset 0 0 1.3rem red;
}

.game-area-wrapper .obstacle {
  background: none !important;
  border: 2px solid orange !important;
}

.game-area-wrapper .play-button {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #ff9777, 0 0 82px #ff9777, 0 0 92px #ff9777, 0 0 102px #ff9777, 0 0 151px #ff9777;
  background: #ff9777;
  -webkit-animation: 1.5s infinite alternate pulsate;
  animation: 1.5s infinite alternate pulsate;
  box-shadow: 0 0 .2rem #fff, 0 0 .2rem #fff, 0 0 2rem #ff9777, 0 0 .8rem #ff9777, 0 0 1.8rem #ff9777, inset 0 0 1.3rem #ff9777;
}

.game-area-wrapper .play-button:hover {
  cursor: pointer !important;
}

.game-area-wrapper .game-area-overlay .gameover-blood {
  font-size: 5em;
  color: red !important;
  -webkit-text-stroke: .1px #f6ba4a !important;
  font-family: nosifer !important;
  transition: all .25s !important;
  -webkit-animation: 2.5s infinite alternate drip !important;
  animation: 2.5s infinite alternate drip !important;
}

@-webkit-keyframes drip {
  to {
    text-shadow: 4px 4px 1px #300000, 4px 6px 1px #400000, 4px 8px 1px #500000, 4px 10px 1px #600000, 4px 12px 1px #700000, 4px 14px 1px maroon, 4px 16px 1px #900000, 4px 18px 1px #a00000, 4px 20px 1px #b00000, 4px 22px 1px #c00000, 4px 24px 1px #d00000, 4px 26px 1px #e00000, 4px 28px 1px #f00000, 4px 30px 1px #fa0000, 4px 32px 1px #fb0000, 4px 34px 1px #fc0000, 4px 36px 1px #fd0000, 4px 38px 1px #fe0000, 4px 40px 2px red;
  }
}

@keyframes drip {
  to {
    text-shadow: 4px 4px 1px #300000, 4px 6px 1px #400000, 4px 8px 1px #500000, 4px 10px 1px #600000, 4px 12px 1px #700000, 4px 14px 1px maroon, 4px 16px 1px #900000, 4px 18px 1px #a00000, 4px 20px 1px #b00000, 4px 22px 1px #c00000, 4px 24px 1px #d00000, 4px 26px 1px #e00000, 4px 28px 1px #f00000, 4px 30px 1px #fa0000, 4px 32px 1px #fb0000, 4px 34px 1px #fc0000, 4px 36px 1px #fd0000, 4px 38px 1px #fe0000, 4px 40px 2px red;
  }
}

@-webkit-keyframes pulsate {
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #ff9777, 0 0 80px #ff9777, 0 0 90px #ff9777, 0 0 100px #ff9777, 0 0 150px #ff9777;
  }

  0% {
    text-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #ff9777, 0 0 45px #ff9777, 0 0 55px #ff9777, 0 0 70px #ff9777, 0 0 80px #ff9777;
  }
}

@keyframes pulsate {
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #ff9777, 0 0 80px #ff9777, 0 0 90px #ff9777, 0 0 100px #ff9777, 0 0 150px #ff9777;
  }

  0% {
    text-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #ff9777, 0 0 45px #ff9777, 0 0 55px #ff9777, 0 0 70px #ff9777, 0 0 80px #ff9777;
  }
}

@-webkit-keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #009688, 0 0 80px #009688, 0 0 90px #009688, 0 0 100px #009688, 0 0 150px #009688;
  }

  20%, 24%, 55% {
    text-shadow: none;
  }
}

@keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #009688, 0 0 80px #009688, 0 0 90px #009688, 0 0 100px #009688, 0 0 150px #009688;
  }

  20%, 24%, 55% {
    text-shadow: none;
  }
}

